html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
}

p {
  font-size: 2rem;
  line-height: 1.5em;
}

a {
  text-decoration: none;
  color: #000000;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  margin: 0 0;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: auto;
}

.contenedor {
  width: 100%;
  margin: 0 0;
}

.contenedor__grid {
  display: grid;
  grid-template-columns: 1fr;
}

@media (min-width: 768px) {
  .contenedor__grid {
    grid-template-columns: 3fr 1fr;
    height: 100rem;
  }
}
.header {
  text-align: center;
  display: block;
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 2px solid #00b289;
  box-shadow: inset 0px -8px 8px rgba(100, 100, 100, 0.2);
}
.header :first-child {
  justify-content: center;
}
.header :last-child {
  align-items: center;
}
.header h1 {
  font-size: 2.5rem;
  color: #0AC1C9;
  margin: 0.5rem;
}
.header .navegacion {
  font-size: 1rem;
  display: flex;
  text-align: center;
  justify-content: space-evenly;
}
.header .navegacion a {
  padding: 1rem 1.5rem 1rem 1.5rem;
  border-radius: 5%;
}
.header .navegacion a:hover {
  box-shadow: inset -2px -2px 2px 2px rgba(0, 178, 137, 0.7);
}
.header .logotipo {
  display: flex;
  align-items: flex-end;
  padding: 0rem;
}
.header .logotipo h2 {
  font-size: 1.5rem;
}

@media (min-width: 768px) {
  .header {
    padding: 2rem;
  }
  .header div {
    display: grid;
    grid-template-columns: 1fr 3fr;
    margin-bottom: 4rem;
  }
  .header div :first-child {
    justify-content: start;
    margin: 0;
  }
  .header h1 {
    font-size: 5rem;
  }
  .header .navegacion {
    font-size: 1.5rem;
  }
  .header .navegacion a {
    padding: 2rem 6rem 2rem 6rem;
  }
  .header .logotipo h2 {
    font-size: 3.5rem;
  }
  .header .logotipo div {
    padding: 0 0;
  }
}
@media (min-width: 992px) {
  h1 {
    font-size: 6rem;
  }
  .navegacion {
    font-size: 1.7rem;
  }
  .navegacion a {
    padding: 2rem 6rem 2rem 6rem;
  }
  .logotipo h2 {
    font-size: 3.5rem;
  }
}
sidebar {
  display: inline;
  background-color: #0AC1C9;
  color: #ffffff;
}

.entrada__sidebar {
  border-bottom: 1px solid #000000;
  padding: 1rem;
  margin: 0 0;
  text-align: end;
}
.entrada__sidebar a {
  font-size: 1.2rem;
  color: #ffffff;
}
.entrada__sidebar a:hover {
  color: #00b289;
}
.entrada__sidebar p {
  text-align: start;
  margin: 0 0;
  color: #ffffff;
  font-size: 1rem;
}

@media (min-width: 768px) {
  .entrada__sidebar p {
    font-size: 1.8rem;
    margin-bottom: 3rem;
  }
  .entrada__sidebar a {
    font-size: 1.5rem;
  }
}
.contenedor__footer {
  color: #ffffff;
  display: block;
  background-color: #000000;
  justify-content: space-between;
}
.contenedor__footer div {
  padding: 3rem;
  display: flex;
  align-items: flex-end;
}
.contenedor__footer .navegacion {
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 2rem 0 0 0;
}
.contenedor__footer .navegacion a {
  color: #ffffff;
  border-right: 1px solid #ffffff;
  padding: 1rem;
  font-size: 1.2rem;
}
.contenedor__footer .navegacion a:hover {
  color: #00b289;
}
.contenedor__footer .logotipo {
  display: flex;
  align-items: flex-end;
  padding: 0rem;
}
.contenedor__footer .logotipo h2 {
  font-size: 1.5rem;
  padding: 0;
  color: #ffffff;
}

footer p {
  text-align: center;
  margin: 0 auto;
  padding: 0.3rem;
  font-size: 1rem;
}

@media (min-width: 768px) {
  .contenedor__footer {
    padding: 2rem;
    display: flex;
  }
  .contenedor__footer .navegacion {
    font-size: 1.5rem;
    margin-right: 5rem;
  }
  .contenedor__footer .logotipo h2 {
    font-size: 3.5rem;
    padding: 0 0;
  }
  footer p {
    text-align: center;
    margin: 0 auto;
    padding: 1rem;
    font-size: 2rem;
  }
}
@media (min-width: 992px) {
  .contenedor__footer .navegacion {
    font-size: 1.6rem;
  }
  .contenedor__footer .logotipo h2 {
    font-size: 3.5rem;
  }
}
.wordFlip {
  margin: 3rem 3rem 0 3rem;
}
.wordFlip h2 {
  font-size: 5rem;
  padding: 3rem 0 3rem 0;
  text-align: center;
}
.wordFlip .formularioWF fieldset {
  display: block;
  border: none;
}
.wordFlip .formularioWF legend, .wordFlip .formularioWF label {
  font-size: 2rem;
  line-height: 3rem;
  margin-bottom: 2rem;
}
.wordFlip .formularioWF input {
  display: block;
  text-align: center;
  font-size: 1.5rem;
  width: 95%;
  height: 5rem;
  margin: 0 auto;
}
.wordFlip #resultadoWF {
  border-top: 1px solid #7D7470;
  padding-top: 1rem;
  text-align: center;
  color: #00b289;
}

.boton {
  text-align: center;
  background-color: #000000;
  color: #ffffff;
}
.boton p {
  padding: 1rem;
  font-size: 3rem;
}
.boton p:hover {
  color: #0ABF2E;
}